}

.footer__links {
  padding: 0;
}

.footer__links a {
  color: #f2f2f2;
  transition: color .5s;
}

.footer__links a:hover {
  color: #fc3;
  transition: color .25s;
}

.header {
  padding-top: 1rem;
  padding-bottom: 1rem;
  margin-top: 0;
  margin-bottom: 0;
}

.header .utility-nav {
  display: none;
}

@media (min-width: 720px) {
  .header .utility-nav {
    display: flex;
  }
  .header .utility-nav li {
    border-right: 1px solid #e5e5e5;
    display: flex;
    align-items: center;
  }
  .header .utility-nav li:last-child {
    border-right: none;
  }
  .header .utility-nav li .icon {
    margin-bottom: 0.5rem;
    font-size: 1.25rem;
  }
}

@media screen and (min-width: 720px) and (min-width: 720px) {
  .header .utility-nav li .icon {
    font-size: 1.3rem;
  }
}

@media screen and (min-width: 720px) and (min-width: 1300px) {
  .header .utility-nav li .icon {
    font-size: 1.35rem;
  }
}

@media (min-width: 720px) {
  .header .utility-nav li a {
    display: block;
    text-decoration: none;
    color: #333;
    background-color: transparent;
    transition: all 0.5s;
    padding: 0.5rem 0.75rem;
    font-size: 0.65rem;
    text-transform: uppercase;
    letter-spacing: 0.1em;
  }
  .header .utility-nav li a:hover {
    background-color: #e5e5e5;
    transition: all 0.25s;
  }
}

.pl-place-holder {
  color: #999;
  border: 4px dashed #e5e5e5;
  padding: 4rem;
}

.pl-place-holder__content {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1.5rem;
  line-height: 1.15;
  font-weight: bold;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
