.page-intro {
  padding-bottom: 2rem;
  margin-bottom: 2rem;
  color: #2f2f2f;
  position: relative;
  @include f4;

  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 2px;
    height: 4px;
    width: 150px;
    background: $oswego-gold;
  }

  p {
    font-weight: 300;
    font-family: $mercury;
    color: lighten(#2f2f2f, 5%);
    margin-bottom: 0;
  }
}


